@mixin border-radius($radius){
  border-radius:$radius;
}

@mixin border($color){
  border:1px solid $color;
}

@mixin circle($width, $color, $background-color){
  display:inline-block;
  @extend %circle;
  width:$width;
  line-height:$width;
  color:$color;
  background-color:$background-color;
}